.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    --padding: 40px 10px 40px 10px;
    background-color: rgba(0, 0, 0, 0.7);
    overflow: hidden;
    overflow-y: auto;
}

.content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
    height: 80%;
    --padding: 60px 20px 50px 20px;
    background-color: white;
    border-radius: 30px;
    filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.7));
}

